* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
p {
  text-align: center;
}

div.app {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  width: 90%;
  margin: 0 auto;
  background-color: #ddd;
}

header {
  flex-basis: 30vh;

  color: white;
}

main {
  display: flex;
  flex-grow: 1;
  padding: 30px 0;
}
section.page {
  flex-grow: 1;
}

aside {
  min-width: 240px;
}

footer {
  background-color: black;
  color: white;
  padding: 10px 0;
  text-align: center;
  text-transform: uppercase;
  margin-bottom: 50px;
}
article {
  margin-top: 40px;
}
article h3 {
  margin-bottom: 10px;
}
article span {
  font-size: 0.7rem;
}
article p {
  text-align: left;
}
