* {
    padding:0;
    margin:0;
    box-sizing: border-box;
}
html {
    font-family:'open sans';
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
p {
  text-align: center;
}

div.app {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  min-height: 100vh;
  width: 90%;
  margin: 0 auto;
  background-color: #ddd;
}

header {
  -webkit-flex-basis: 30vh;
          flex-basis: 30vh;

  color: white;
}

main {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-grow: 1;
          flex-grow: 1;
  padding: 30px 0;
}
section.page {
  -webkit-flex-grow: 1;
          flex-grow: 1;
}

aside {
  min-width: 240px;
}

footer {
  background-color: black;
  color: white;
  padding: 10px 0;
  text-align: center;
  text-transform: uppercase;
  margin-bottom: 50px;
}
article {
  margin-top: 40px;
}
article h3 {
  margin-bottom: 10px;
}
article span {
  font-size: 0.7rem;
}
article p {
  text-align: left;
}

header {
  position: relative;
  overflow: hidden;
}
header img {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  min-width: 100%;
  min-height: 100%;
}

.contact textarea {
  font-size: 16px;
  width: 80%;
  min-height: 200px;
  margin: 20px 0;
  line-height: 1.5;
}

.contact button {
  display: block;
  margin: 0 20% 0 auto;
  border: 2px solid black;
  background-color: #fff;
  padding: 10px 20px;
  cursor: pointer;
  font-size: 20px;
  -webkit-transition: 0.2s;
  transition: 0.2s;
}

.contact button:hover {
  color: yellow;
  background-color: black;
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

ul li a {
  display: block;
  text-decoration: none;
  color: black;
  padding: 10px 0;
  text-transform: uppercase;
  font-family: arial;
}
ul li a:hover {
  color: red;
}

nav {
  padding: 0 30px 30px 0;
}

.main ul {
  list-style: none;
}

nav.main a {
  display: block;
  text-decoration: none;
  color: black;
  text-transform: uppercase;
  font-weight: bold;
  padding: 10px;
  -webkit-transition: 0.2s;
  transition: 0.2s;
}

nav.main a:hover {
  -webkit-transform: translateX(20px);
          transform: translateX(20px);
}

nav.main a.active {
  border-left: 10px gray solid;
  -webkit-transform: translateX(0);
          transform: translateX(0);
}

nav.main li:nth-child(1) a.active {
  border-left-color: green;
}
nav.main li:nth-child(2) a.active {
  border-left-color: blue;
}
nav.main li:nth-child(3) a.active {
  border-left-color: red;
}
nav.main li:nth-child(4) a.active {
  border-left-color: yellow;
}

footer span {
  color: red;
}

footer a {
  font-size: 15px;
}

